<template>
  <div>
    <div class="release">
      <div class="r-relBox">
        <div class="r-title"></div>
        <wv-group>
          <wv-textarea placeholder="" :rows="2" :max-length="30" v-model.trim="courseValue"></wv-textarea>
        </wv-group>
      </div>

      <div class="r-relBox">
        <div class="r-title"></div>
        <!--<textarea class="r-topics" rows="8" maxlength="300" placeholder=""></textarea>-->
        <wv-group>
          <wv-textarea placeholder="" :rows="8" :max-length="300" v-model.trim="contentValue"></wv-textarea>
        </wv-group>
      </div>

      <div class="liveWay">
        <div class="r-title"></div>
        <div class="live" :class="{'active':sharePlatform=== 1 }" @click="sharePlatform=1">YY</div>
        <div class="live" :class="{'active':sharePlatform=== 2 }" @click="sharePlatform=2"></div>
      </div>

      <div class="courseTime">
        <div class="r-title"></div>
        <wv-button @click="ticketPickerShow = true" v-text="courseTime"></wv-button>
        <wv-picker v-model="ticketPickerShow" :slots="ticketSlots" @change="onChange"></wv-picker>
        <i class="iconfont">&#xe612;</i>
      </div>

      <div class="coursePrice">
        <div class="r-title"></div>
        <wv-group>
          <wv-input placeholder="" type="number" v-model="priceNumber"></wv-input>
        </wv-group>
        <div class="t-right">
          <router-link to="/earnings" tag="div"></router-link>
        </div>

      </div>

      <div class="r-agreement">
        <input type="checkbox" :checked="isChecked" v-model="isChecked">
        <span @click="isChecked=true"><router-link to="/agreement"></router-link></span>
      </div>

      <div class="r-submit">
        <input type="button" value="" @click="submitRelease">
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import WeVue from 'we-vue';
  export default {
    data() {
      return {
        isChecked: true,
        courseValue: this.value,
        contentValue: this.value,
        priceNumber: this.value,
        sharePlatform: 1,
        courseTime: '30',
        ticketPickerShow: false,
        ticketSlots: [
          {
            values: [
              '15',
              '30',
              '60',
              '90'
            ]
          }
        ]
      };
    },
    methods: {
      onChange (picker, value) {
        this.$nextTick(() => {
          this.courseTime = value.join('.');
        });
      },
      tips (msg) {
        WeVue.TopTips({
          message: msg,
          duration: 1500
        });
      },
      submitRelease() {
        if (!this.courseValue) {
          this.tips('');
          return false;
        } else if (!this.contentValue) {
          this.tips('');
          return false;
        } else if (!/^\d+(\.\d{1,2})?$/.test(this.priceNumber)) {
          this.tips(',');
          return false;
        } else if (!this.isChecked) {
          this.tips('');
          return false;
        } else {
          WeVue.Dialog.confirm(WeVue.Dialog.setDefaults({
            title: '',
            message: '',
            confirmText: '',
            cancelText: '',
            showCancelBtn: true
          })).then(action => {
            console.log(action);
            this.$router.push('./releaseSuccess');
          });
        }
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .release
    padding 15px 10px 100px 10px
    .r-title
      color #cc1138
      font-size 15px
      padding 15px 0
    .r-relBox
      display flex
      flex-direction column
      .weui-cells
        margin 0
        font-size 12px
        border solid 1px #e3e2e1
        border-radius 3px
        .weui-cell
          padding 5px
      .r-topics
        color #000
        font-size 12px
        flex-grow 1
        padding 5px
        border solid 1px #e3e2e1
        border-radius 3px
        resize none
        outline none
        box-shadow none
    .liveWay
      display flex
      align-items center
      padding 20px 0 0 0
      .r-title
        margin-right 10px
      .live
        border solid 1px #cc1138
        border-radius 3px
        color #cc1138
        font-size 13px
        padding 5px 8px
        margin 0 5px
      .active
        background #cc1138
        color #f3f3f3
    .courseTime
      display flex
      justify-content space-between
      align-items center
      padding 10px 0
      button.weui-btn
        flex-grow 1
        width auto
        background none
        font-size 14px
        margin-right 0
        text-align right
        padding-right 10px
      button.weui-btn:hover
        background none
      .weui-btn:after
        content none
      i
        margin-top -2px
      .r-title
        margin-right 15px
      input
        flex-grow: 1;
        font-size 12px
        border solid 1px #e3e2e1
        height 16px
        line-height 16px
        border-radius 3px
        padding 5px 3px
    .coursePrice
      display flex
      justify-content flex-start
      align-items center
      padding 10px 0
      .weui-cells
        margin 0
        flex-grow 1
        margin-right 10px
        border solid 1px #e3e2e1
        font-size 12px
        border-radius 3px
        .weui-cell
          padding 0
      .r-title
        margin-right 15px
        font-size 15px
        white-space nowrap
      input
        font-size 12px
        width 100%
        border none
        border-radius 3px
        height 16px
        line-height 16px
        padding 5px 3px
      .t-right
        font-size 14px
        display flex
        flex-grow 1
        flex-wrap nowrap
        justify-content space-around
        div
          white-space nowrap
    .r-agreement
      input
        vertical-align middle
        border solid 1px #c8c6c4
      span
        font-size 12px
        a
          color royalblue
    .r-submit
      padding 40px 0 0 0
      text-align center
      input
        background #cc1138
        border none
        outline none
        border-radius 3px
        font-size 14px
        color #ebebeb
        padding 5px 30px
</style>




